import levels from '../assets/json/levels.json';

export default class State {
	constructor({ assets }) {
		this._assets = assets;

		this._level = 15;
		this._gold = 0;
	}

	increaseLevel() {
		const nextLevel = this._level + 1;
		if (levels.maps[nextLevel]) this._level = nextLevel;
		else this._level = 0;
	}

	resetLevel() {
		this._level = 0;
	}

	setLevel(level) {
		this._level = level;
	}

	getLevel() {
		return this._level;
	}
}