import Draw from './Draw';
import Text from './Text';
import Button from './Button';
import Color from './Color';

export default class OpenScene {
  constructor({ canvas, assets, state, sceneManager }) {
    this._canvas = canvas;
		this._assets = assets;
		this._state = state;
    this._sceneManager = sceneManager;
    this._levelCounter = this._state.getLevel();

    this._startButton = null;
    this._initButton();
  }

  update(time) {
    
  }

  render(ctx) {
    Draw.rect(ctx, 0, 0, this._canvas.width, this._canvas.height, Color.blueDeep.key);

    this._renderTitle(ctx);
    this._renderLevelCounter(ctx);
    this._startButton.render(ctx);
  }

  _renderTitle(ctx) {
    OpenScene.title.split(' ').forEach((string, index) => {
      const { textWidth } = Text.calcTextMetrics(ctx, string, OpenScene.titleFontSize);

      const stringPositionX = this._canvas.width / 2 - textWidth / 2;
      const stringPositionY = OpenScene.titlePositionY + (index * OpenScene.titleLineHeight) + OpenScene.titleFontSize;

      Draw.text(ctx, stringPositionX, stringPositionY, string, OpenScene.titleFontSize, Color.white.key);
    })
  }

  _renderLevelCounter(ctx) {
    const { textWidth: levelCounterHintWidth } = Text.calcTextMetrics(ctx, OpenScene.levelCounterHint, OpenScene.levelCounterHintFontSize);
    const levelCounterHintPositionX = this._canvas.width / 2 - levelCounterHintWidth / 2;
    const levelCounterHintPositionY = OpenScene.levelCounterHintPositionY + OpenScene.levelCounterHintFontSize;
    Draw.text(ctx, levelCounterHintPositionX, levelCounterHintPositionY, OpenScene.levelCounterHint, OpenScene.levelCounterHintFontSize, Color.yellow.key);

    const levelCounter = this._levelCounter + 1;
    const { textWidth: levelCounterWidth } = Text.calcTextMetrics(ctx, levelCounter, OpenScene.levelCounterFontSize);
    const levelCounterPositionX = this._canvas.width / 2 - levelCounterWidth / 2;
    const levelCounterPositionY = OpenScene.levelCounterPositionY + OpenScene.levelCounterFontSize;
    Draw.text(ctx, levelCounterPositionX, levelCounterPositionY, levelCounter, OpenScene.levelCounterFontSize, Color.yellow.key);
  }

  handleClick({ position }) {
    if (this._startButton.isPressed(position)) this._sceneManager.showCoreScene();
  }

  handleStartDragging() {

  }

  handleDragging() {

  }

  handleEndDragging() {

  }

  _initButton() {
    this._startButton = new Button({
      position: {
        x: this._canvas.width / 2 - OpenScene.startButtonWidth / 2,
        y: OpenScene.startButtonPositionY,
      },
      size: {
        width: OpenScene.startButtonWidth,
        height: OpenScene.buttonsHeight,
      },
      label: '',
    })
  }

  static title = 'Colored Tiles';
  static titleFontSize = 96;
  static titleLineHeight = 100;
  static titlePositionY = 329;

  static levelCounterHint = ':';
  static levelCounterHintFontSize = 48;
  static levelCounterHintPositionY = 609;
  static levelCounterFontSize = 96;
  static levelCounterPositionY = 667;

  static startButtonPositionY = 843;
  static startButtonWidth = 194;
  static buttonsHeight = 74;
}